import React, { useEffect, useRef } from 'react';
import { Scene, WebGLRenderer, Color, TextureLoader, PerspectiveCamera, AmbientLight, PointLight, PointLightHelper, sRGBEncoding, PMREMGenerator, TorusKnotBufferGeometry, MeshStandardMaterial, Mesh, DefaultLoadingManager, ACESFilmicToneMapping } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


const TestScene = props => {
  const { initialScale, modelUrl } = props;
  const anchorGltf = useRef();
  

  useEffect(() => {
    let isMounted = true;
    var camera, controls, scene, renderer;

    const onWindowResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(window.innerWidth, window.innerHeight);
    }

    const initialize = () => {
      scene = new Scene();
      scene.background = new Color(0xcccccc);

      renderer = new WebGLRenderer({ antialias: true });
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setSize(window.innerWidth, window.innerHeight);
      document.body.appendChild(renderer.domElement);

      renderer.toneMapping = ACESFilmicToneMapping;
      renderer.outputEncoding = sRGBEncoding;

      const pmremGenerator = new PMREMGenerator(renderer);

      pmremGenerator.compileEquirectangularShader();

      const geometry = new TorusKnotBufferGeometry(1.8, 0.8, 150, 20);
      const material = new MeshStandardMaterial({
        metalness: 1,
        roughness: 0,
        envMapIntensity: 1.0
      });

      const torusMesh = new Mesh(geometry, material);

      // scene.add(torusMesh);

      DefaultLoadingManager.onLoad = () => {
        pmremGenerator.dispose();
      };
  

      camera = new PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 1000);
      // camera.position.set(400, 200, 0);

      // controls

      controls = new OrbitControls(camera, renderer.domElement);

      controls.enableDamping = true;
      controls.dampingFactor = 0.05;

      controls.screenSpacePanning = false;

      controls.minDistance = 1;
      controls.maxDistance = 20;

      // controls.maxPolarAngle = Math.PI / 2;

      // lights

      var light3 = new AmbientLight(0xffffff, 0.5);
      scene.add(light3);

      var light4 = new PointLight(0xffffff, 1);
      light4.position.set(1, 1, 1);
      var helper1 = new PointLightHelper(light4, 1);
      scene.add(light4);
      scene.add(helper1);

      var light5 = new PointLight(0xffffff, 1);
      light5.position.set(-1, 1, -1);
      var helper2 = new PointLightHelper(light5, 1);
      scene.add(light5);
      scene.add(helper2);

      var light6 = new PointLight(0xffffff, 1);
      light6.position.set(-1, -1, -1);
      var helper3 = new PointLightHelper(light6, 1);
      scene.add(light6);
      scene.add(helper3);


      window.addEventListener('resize', onWindowResize, false);

      document.getElementById('ar-container').appendChild(renderer.domElement);

      new TextureLoader().load(`${process.env.PUBLIC_URL}/models/anchor/Textures/equirectangular.png`, texture => {
        texture.encoding = sRGBEncoding;
        const pngCubeRenderTarget = pmremGenerator.fromEquirectangular(texture);

        scene.environment = pngCubeRenderTarget.texture;

        new GLTFLoader()
          .load(modelUrl, gltf => {
            anchorGltf.current = gltf;
            anchorGltf.current.scene.scale.set(initialScale, initialScale, initialScale);
            scene.add(anchorGltf.current.scene);
            texture.dispose();
          });
      });
    }

    const update = () => {
      controls.update();
    };

    const render = () => {
      renderer.render(scene, camera);
    };
    
    
    const animate = () => {
      if (isMounted) {
        requestAnimationFrame(animate);
        update();
        render();
      }
    };

    initialize();
    animate();

    return () => {
      isMounted = false;
      document.getElementById('ar-container').removeChild(renderer.domElement);
    };
  }, [modelUrl, initialScale]);

  return (
    <div>
       Test
    </div>
  );
};

TestScene.defaultProps = {
  initialScale: 0.004,
  audioUrl: `${process.env.PUBLIC_URL}/audio/anchor.mp3`,
  modelUrl: `${process.env.PUBLIC_URL}/models/anchor/MiraiKoukai2.gltf`,
  patternUrl: `${process.env.PUBLIC_URL}/data/hiro.patt`,
  cameraParametersUrl: `${process.env.PUBLIC_URL}/data/camera_para.dat`
};

export default TestScene;
